<template>
  <div class="login">
    <div class="oauth">
      <p>Sign in with social accounts</p>
      <a v-for="item in oauth" :href="item.url">
        <img :src="'/img/oauth/' + item.name + '.svg'" :alt="item.name">
      </a>
    </div>
    <div class="or">- OR -</div>
    <form action="signin" class="signin" @submit.prevent="signin">
      <input type="text" placeholder="Username">
      <input type="password" placeholder="Password">
      <input type="submit" value="Sign in">
    </form>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      oauth: [
        {
          name: 'github',
          url: 'https://github.com/login/oauth/authorize?client_id=6e6d16dc32560e7d5b34&scope=user'
        }
      ],
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



